import { Component, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the TaptopayActivatePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-taptopay-activate',
  templateUrl: 'taptopay-activate.html',
})
export class TaptopayActivatePage {
  @ViewChild('myBar') dynamic: ElementRef;
  imgSlide = [
    {
      img: "assets/imgs/taptopay/ttp_logo1.svg"
    },
    {
      img: "assets/imgs/taptopay/ttp_logo2.svg"
    },
    {
      img: "assets/imgs/taptopay/ttp_logo3.svg"
    }
  ]
  btnTry;
  width = 0;
  interval;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TaptopayActivatePage');
    this.btnTry = document.getElementById("myBar");
  }

  progress(num) {
    this.progressRun(num)
  }

  progressRun(num) {
    if (num == 20) {
      this.width = 0;
    }
    this.interval = setInterval(() => {
      if (this.width >= num) {
        clearInterval(this.interval);
      } else {
        this.width++; 
        this.btnTry.style.width = this.width + '%'; 
        this.btnTry.innerHTML = this.width * 1  + '%';
      }
    }, 20);
  }

  // frame() {
  //   if (this.width >= 100) {
  //     clearInterval(this.interval);
  //   } else {
  //     this.width++; 
  //     this.elem.style.width = this.width + '%'; 
  //     this.elem.innerHTML = this.width * 1  + '%';
  //   }
  // }

}



